/* ==========================================================================
   Layout mixin style - begin
   ========================================================================== */

@mixin evcRelativeLayout() {
	position: relative;
	display: inline-block;
	width: 100%;
	vertical-align: top;
}

@mixin evcAbsoluteLayout() {
	position: absolute;
	display: block;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
}

@mixin evcFlexBoxLayout($position: null, $align-items: null, $justify-content: null) {
	@if ($position) {
		position: $position;
	}
	
	@include evcFlexLayout();
	
	@if ($align-items) {
		@include evcFlexAlignItems($align-items);
	}
	
	@if ($justify-content) {
		@include evcFlexJustifyContent($justify-content);
	}
}

@mixin evcFlexContainer($align-items: null, $justify-content: null, $flex-direction: null, $flex-wrap: null, $align-content: null) {
	@include evcFlexBoxLayout(null, $align-items, $justify-content);
	
	@if ($flex-direction) {
		flex-direction: $flex-direction;
	}
	
	@if ($flex-wrap) {
		flex-wrap: $flex-wrap;
	}
	
	@if ($align-content) {
		align-content: $align-content;
	}
}

@mixin evcFlexLayout() {
	display: -webkit-box;
	display: -webkit-flex;
	display: -ms-flexbox;
	display: flex;
}

@mixin evcFlexAlignItems($align-items) {
	$older-align-items: $align-items;
	
	@if ($align-items == 'flex-start') {
		$older-align-items: start;
	} @else if ($align-items == 'flex-end') {
		$older-align-items: end;
	}
	
	-webkit-box-align: $older-align-items;
	-webkit-align-items: $align-items;
	-ms-flex-align: $older-align-items;
	align-items: $align-items;
}

@mixin evcFlexJustifyContent($justify-content) {
	$older-justify-content: $justify-content;
	
	@if ($justify-content == 'flex-start') {
		$older-justify-content: start;
	} @else if ($justify-content == 'flex-end') {
		$older-justify-content: end;
	}
	
	-webkit-box-pack: $older-justify-content;
	-webkit-justify-content: $justify-content;
	-ms-flex-pack: $older-justify-content;
	justify-content: $justify-content;
}

@mixin evcTableLayout() {
	position: relative;
	display: table;
	width: 100%;
	height: 100%;
}

@mixin evcTableCellLayout() {
	position: relative;
	display: table-cell;
	width: 100%;
	height: 100%;
	vertical-align: middle;
}

/* ==========================================================================
   Layout mixin style - end
   ========================================================================== */

/* ==========================================================================
   Animation mixin style - begin
   ========================================================================== */

@mixin evcTransition($transition-param...) {
	-webkit-transition: $transition-param;
	-moz-transition: $transition-param;
	transition: $transition-param;
}

@mixin evcTransitionDelay($transition-param...) {
	-webkit-transition-delay: $transition-param;
	-moz-transition-delay: $transition-param;
	transition-delay: $transition-param;
}

@mixin evcTransitionTransform($transition-param...) {
	-webkit-transition: -webkit-transform $transition-param;
	-moz-transition: -moz-transform $transition-param;
	transition: transform $transition-param;
}

@mixin evcTransform($transform-param...) {
	-webkit-transform: $transform-param;
	-moz-transform: $transform-param;
	transform: $transform-param;
}

@mixin evcTransformStyle($transform-param...) {
	-webkit-transform-style: $transform-param;
	-moz-transform-style: $transform-param;
	transform-style: $transform-param;
}

@mixin evcTransformOrigin($transform-param...) {
	-webkit-transform-origin: $transform-param;
	-moz-transform-origin: $transform-param;
	transform-origin: $transform-param;
}

@mixin evcAnimation($animation-param...) {
	-webkit-animation: $animation-param;
	-moz-animation: $animation-param;
	animation: $animation-param;
}

@mixin evcAnimationDelay($animation-param...) {
	-webkit-animation-delay: $animation-param;
	-moz-animation-delay: $animation-param;
	animation-delay: $animation-param;
}

/* ==========================================================================
   Animation mixin style - end
   ========================================================================== */

/* ==========================================================================
   Specific mixin style - end
   ========================================================================== */

@mixin evcOverlayContentStyle($color: rgba($dark-color, .3), $transition: opacity .3s ease-in-out) {
	@include evcAbsoluteLayout();
	content: '';
	background-color: $color;
	opacity: 0;
	@include evcTransition($transition);
}

@mixin evcOverlayStyle($is_item: false) {
	
	@if ($is_item) {
		.touch & {
			cursor: pointer;
		}
	}
	
	&:hover {
		
		&:after {
			opacity: 1;
		}
	}
	
	&:after {
		@include evcOverlayContentStyle();
	}
}

/* ==========================================================================
   Specific mixin style - end
   ========================================================================== */

/* ==========================================================================
   Responsive mixin style - begin
   ========================================================================== */

@mixin laptop-screen {
	@media only screen and (max-width: $laptop-screen) {
		@content;
	}
}

@mixin laptop-small-screen {
	@media only screen and (max-width: $laptop-small-screen) {
		@content;
	}
}

@mixin mac-screen {
	@media only screen and (max-width: $mac-screen) {
		@content;
	}
}

@mixin ipad-landscape-screen {
	@media only screen and (max-width: $ipad-landscape-screen) {
		@content;
	}
}

@mixin ipad-portrait-screen {
	@media only screen and (max-width: $ipad-portrait-screen) {
		@content;
	}
}

@mixin phone-landscape-screen {
	@media only screen and (max-width: $phone-landscape-screen) {
		@content;
	}
}

@mixin phone-portrait-screen {
	@media only screen and (max-width: $phone-portrait-screen) {
		@content;
	}
}

/* ==========================================================================
   Responsive mixin style - end
   ========================================================================== */